export interface TicketDefinition {
  /**
   * Ticket definition ID.
   * @format GUID
   */
  id?: string;
  /** Ticket price. */
  price?: Money;
  /** Whether the ticket is free (read only). */
  free?: boolean;
  /** Ticket name. */
  name?: string;
  /** Ticket description. */
  description?: string;
  /**
   * Limit of tickets that can be purchased per checkout.
   * Set to 20 for unlimited ticket definition.
   * @max 20
   */
  limitPerCheckout?: number;
  /** Custom sort index. */
  orderIndex?: number;
  /** Policy information plain text block, as printed on the ticket. */
  policy?: string;
  /** Sensitive dashboard data. */
  dashboard?: Dashboard;
  /**
   * Event ID associated with the ticket.
   * @format GUID
   */
  eventId?: string;
  /**
   * Configuration of the fixed-rate Wix service fee that is applied at checkout to each ticket sold.
   * @readonly
   */
  wixFeeConfig?: WixFeeConfig;
  /** Ticket sale period. */
  salePeriod?: TicketSalePeriod;
  /**
   * Ticket sale status.
   * @readonly
   */
  saleStatus?: TicketSaleStatusWithLiterals;
  /** Ticket state. */
  state?: TicketDefinitionStateEnumStateWithLiterals[];
  /** Ticket pricing. */
  pricing?: TicketPricing;
}

export interface Money {
  /**
   * *Deprecated:** Use `value` instead.
   * @format DECIMAL_VALUE
   * @deprecated
   */
  amount?: string;
  /**
   * 3-letter currency code in [ISO-4217 alphabetic](https://en.wikipedia.org/wiki/ISO_4217#Active_codes) format. For example, `USD`.
   * @format CURRENCY
   */
  currency?: string;
  /**
   * Monetary amount. Decimal string with a period as a decimal separator (e.g., 3.99). Optionally, starts with a single (-), to indicate that the amount is negative.
   * @format DECIMAL_VALUE
   */
  value?: string | null;
}

export interface Dashboard {
  /** Whether ticket is hidden and cannot be sold. */
  hidden?: boolean;
  /**
   * Number of tickets sold and reserved.
   * @deprecated
   */
  sold?: number;
  /** Whether the ticket has limited quantity. */
  limited?: boolean;
  /** Ticket limit. `NULL` for unlimited ticket definitions. */
  quantity?: number | null;
  /** Number of unsold tickets. `NULL` for unlimited ticket definitions. */
  unsold?: number | null;
  /** Number of tickets sold. */
  ticketsSold?: number;
  /** Number of tickets reserved. */
  ticketsReserved?: number;
}

export interface WixFeeConfig {
  /** Fee calculation method. */
  type?: FeeTypeWithLiterals;
}

export enum FeeType {
  /** Fee is added to the ticket price at checkout. */
  FEE_ADDED = 'FEE_ADDED',
  /** Seller absorbs the fee. It's deducted from the ticket price. */
  FEE_INCLUDED = 'FEE_INCLUDED',
  /** Fee is added to the ticket price at checkout. */
  FEE_ADDED_AT_CHECKOUT = 'FEE_ADDED_AT_CHECKOUT',
}

/** @enumType */
export type FeeTypeWithLiterals =
  | FeeType
  | 'FEE_ADDED'
  | 'FEE_INCLUDED'
  | 'FEE_ADDED_AT_CHECKOUT';

export interface TicketSalePeriod {
  /** Ticket sale start timestamp. */
  startDate?: Date | null;
  /** Ticket sale end timestamp. */
  endDate?: Date | null;
  /** Whether to hide this ticket if it isn't on sale. */
  hideNotOnSale?: boolean;
}

export enum TicketSaleStatus {
  /** Ticket sale is scheduled to start. */
  SALE_SCHEDULED = 'SALE_SCHEDULED',
  /** Ticket sale has started. */
  SALE_STARTED = 'SALE_STARTED',
  /** Ticket sale has ended. */
  SALE_ENDED = 'SALE_ENDED',
}

/** @enumType */
export type TicketSaleStatusWithLiterals =
  | TicketSaleStatus
  | 'SALE_SCHEDULED'
  | 'SALE_STARTED'
  | 'SALE_ENDED';

export enum TicketDefinitionStateEnumState {
  INCLUDE_HIDDEN_NOT_ON_SALE = 'INCLUDE_HIDDEN_NOT_ON_SALE',
}

/** @enumType */
export type TicketDefinitionStateEnumStateWithLiterals =
  | TicketDefinitionStateEnumState
  | 'INCLUDE_HIDDEN_NOT_ON_SALE';

export interface TicketPricing extends TicketPricingPriceOneOf {
  /** Ticket price which is read only. */
  fixedPrice?: Money;
  /** Min price per ticket, customizable. */
  minPrice?: Money;
  /** Ticket pricing options. */
  pricingOptions?: PricingOptions;
  /**
   * Ticket pricing type.
   * @readonly
   */
  pricingType?: TypeWithLiterals;
}

/** @oneof */
export interface TicketPricingPriceOneOf {
  /** Ticket price which is read only. */
  fixedPrice?: Money;
  /** Min price per ticket, customizable. */
  minPrice?: Money;
  /** Ticket pricing options. */
  pricingOptions?: PricingOptions;
}

export interface PricingOptions {
  /** Multiple ticket pricing options. */
  options?: PricingOption[];
}

export interface PricingOption {
  /**
   * Ticket pricing option ID.
   * @format GUID
   */
  id?: string | null;
  /**
   * Ticket pricing option name.
   * @minLength 1
   * @maxLength 200
   */
  name?: string | null;
  /** Ticket pricing option price. */
  price?: Money;
}

export enum Type {
  STANDARD = 'STANDARD',
  DONATION = 'DONATION',
}

/** @enumType */
export type TypeWithLiterals = Type | 'STANDARD' | 'DONATION';

